body {
  margin: 0;
  padding: 0;
  color: #1a202c;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
}

.container {
  max-width: 80rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.btn-primary {
  background-color: #D60D0D;
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem;
  display: inline-block;
  transition: background-color 0.2s;
}

.btn-primary:hover {
  background-color: #C0D9DA;
}

.btn-secondary {
  background-color: #C0D9DA;
  color: #D60D0D;
  padding: 0.75rem 1.5rem;
  border-radius: 0.375rem;
  display: inline-block;
  transition: all 0.2s;
}

.btn-secondary:hover {
  background-color: #D60D0D;
  color: white;
}

.section-title {
  font-size: 2.25rem;
  font-family: 'Playfair Display', serif;
  color: #D60D0D;
  margin-bottom: 1rem;
}

.section-subtitle {
  font-size: 1.25rem;
  color: #4B5563;
  max-width: 42rem;
  margin-left: auto;
  margin-right: auto;
}

.text-shadow {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

.font-display {
  font-family: 'Playfair Display', serif;
}

.App {
  text-align: center;
}

/* Responsive utilities */
@media (min-width: 640px) {
  .container {
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (min-width: 1024px) {
  .container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

/* Grid utilities */
.grid {
  display: grid;
}

.grid-cols-1 {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

@media (min-width: 768px) {
  .md\\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (min-width: 1024px) {
  .lg\\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

/* Spacing utilities */
.gap-8 {
  gap: 2rem;
}

.gap-12 {
  gap: 3rem;
}

.space-y-6 > * + * {
  margin-top: 1.5rem;
}

.space-y-8 > * + * {
  margin-top: 2rem;
}

/* Background colors */
.bg-primary {
  background-color: #D60D0D;
}

.bg-secondary {
  background-color: #C0D9DA;
}

.bg-gray-50 {
  background-color: #F9FAFB;
}

.bg-white {
  background-color: white;
}

/* Text colors */
.text-primary {
  color: #D60D0D;
}

.text-gray-600 {
  color: #4B5563;
}

.text-gray-900 {
  color: #1A202C;
}

.text-white {
  color: white;
}
